.sticky-file-widget{display:inline-block}.sticky-preview{width:150px;height:150px;display:inline-block;position:relative;text-align:center;border:1px solid rgba(0,0,0,0.1);vertical-align:top;margin:0 20px 20px 0}.sticky-preview__remove-button{width:20px;height:20px;position:absolute;top:-10px;right:-10px;display:block;border:1px solid rgba(0,0,0,0.1);border-radius:50%;background-color:white}.sticky-preview__remove-button::before{display:inline-block;content:"✕";width:100%;height:100%;line-height:1;cursor:pointer}.sticky-upload-button{width:150px;height:150px;background-color:beige;display:inline-block;background-image:url("data:image/png;base64,");background-repeat:no-repeat;background-position:center;background-size:60%;border:1px solid rgba(0,0,0,0.1);cursor:pointer}.sticky-upload-button input[type=file]{display:none}.image-preview{max-width:100%;max-height:100%;vertical-align:middle;display:inline-block;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)}.document-preview{width:150px;height:150px;background-image:url("data:image/png;base64,");background-repeat:no-repeat;background-position:center;background-size:60%}
